<template>
  <div class="hello">
    <el-card shadow="never" style="margin-bottom: 20px">
      <el-form :model="form" inline size="small" label-position="right">
        <el-form-item label="年份" prop="year" style="margin-bottom: 0">
          <el-date-picker
            v-model="form.year"
            type="year"
            value-format="yyyy"
            style="width: 100px"
            :clearable="false"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="月份" prop="month" style="margin-bottom: 0">
          <el-select v-model="form.month" style="width: 80px">
            <el-option
              v-for="item in months"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="讲员" prop="preachers" style="margin-bottom: 0">
          <el-select v-model="form.preachers" multiple collapse-tags clearableß>
            <el-option
              v-for="item in preacherList"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="分区数" prop="partNum" style="margin-bottom: 0">
          <el-input-number
            v-model="form.partNum"
            :min="1"
            :max="20"
            label="分区数"
            style="width: 130px"
            @change="changePartNum"
          ></el-input-number>
        </el-form-item>
        <el-form-item style="margin-bottom: 0">
          <el-button type="primary" @click="generate()">生成派单</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <el-table :data="tableData" size="mediumn" border stripe max-height="600">
      <el-table-column
        v-for="(col, index) in columns"
        :key="index"
        :prop="col.prop"
        :label="col.label"
      >
        <template slot-scope="{ row }">
          <template v-if="col.prop === 'name'">
            {{ row[col.prop] }}
            <el-select
              v-model="row.part"
              size="mini"
              placeholder="请选择"
              style="float: right; width: 100px"
            >
              <el-option
                v-for="item in parts"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </template>
          <template v-else>
            <template v-for="(preacher, index) in preacherList">
              <el-tag
                v-if="preacher.value === row[col.prop]"
                :type="preacher.value === 1 ? 'danger' : 'success'"
                :key="index"
                size="small"
                >{{ preacher.name }}</el-tag
              >
            </template>
          </template>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
const groupList = [
  { name: "朱梅", part: 1 },
  { name: "大琳", part: 1 },
  { name: "朱亚花", part: 1 },
  { name: "华星花", part: 1 },
  { name: "须建芬", part: 2 },
  { name: "姚花琴", part: 2 },
  { name: "王闵夏", part: 2 },
  { name: "赵小萍", part: 2 },
  { name: "赵萍", part: 3 },
  { name: "鲁玉月", part: 3 },
  { name: "李雪清", part: 3 },
  { name: "袁理珍", part: 3 },
  { name: "赵敬兵", part: 3 },
  { name: "王利民", part: 4 },
  { name: "刘彦馨", part: 4 },
  { name: "刘登江", part: 4 },
  { name: "赵其友", part: 4 },
  { name: "陈克君", part: 4 },
];
const preacherList = [
  { name: "圣餐", value: 1 },
  { name: "总会", value: 2 },
  { name: "朱梅", value: 10 },
  { name: "大琳", value: 11 },
  { name: "朱亚花", value: 12 },
  { name: "华星花", value: 13 },
  { name: "须建芬", value: 14 },
  { name: "姚花琴", value: 15 },
  { name: "王闵夏", value: 16 },
  { name: "赵小萍", value: 17 },
  { name: "赵萍", value: 18 },
  { name: "鲁玉月", value: 19 },
  { name: "李雪清", value: 20 },
  { name: "袁理珍", value: 21 },
  { name: "赵敬兵", value: 22 },
  { name: "王利民", value: 23 },
  { name: "刘彦馨", value: 24 },
  { name: "刘登江", value: 25 },
  { name: "赵其友", value: 26 },
  { name: "陈克君", value: 27 },
];
export default {
  name: "HelloWorld",
  data() {
    return {
      form: {
        year: "2022",
        month: 7,
        preachers: [1, 2],
        partNum: 4,
      },
      months: [
        { label: "1月", value: 1 },
        { label: "2月", value: 2 },
        { label: "3月", value: 3 },
        { label: "4月", value: 4 },
        { label: "5月", value: 5 },
        { label: "6月", value: 6 },
        { label: "7月", value: 7 },
        { label: "8月", value: 8 },
        { label: "9月", value: 9 },
        { label: "10月", value: 10 },
        { label: "11月", value: 11 },
        { label: "12月", value: 12 },
      ],
      preacherList: [...preacherList],
      parts: [
        { label: "1区", value: 1 },
        { label: "2区", value: 2 },
        { label: "3区", value: 3 },
        { label: "4区", value: 4 },
      ],
      tableData: [],
    };
  },
  computed: {
    columns() {
      const columns = [{ label: "组名", prop: "name" }];
      let result = this.getWeekDay(this.form.year, this.form.month, 0);
      result.forEach((item) => {
        const obj = { label: item, prop: item };
        columns.push(obj);
      });
      return columns;
    },
  },
  methods: {
    // 获取一个月有多少天
    getMonthLength(date) {
      let d = new Date(date);
      // 获取月份的下个月
      d.setMonth(d.getMonth() + 1);
      // 设置为下个月第一天
      d.setDate("1");
      // 设置时间为下月的前一天
      d.setDate(d.getDate() - 1);
      // 返回天数
      return d.getDate();
    },
    // 获取本月的周六日 type 0-周日
    getWeekDay(year, month, type) {
      year = Number(year);
      var arr = [];
      var days = this.getMonthLength(`${year}-${month}`);
      for (var j = 1; j <= days; j++) {
        if (new Date(`${year}-${month}-${j}`).getDay() === type) {
          arr.push(`${year}-${month}-${j}`);
        }
      }
      return arr;
    },
    changeDate() {
      this.init();
    },
    resetData() {
      this.form.preachers = this.preacherList.map((i) => i.value);
      this.tableData = [];
    },
    init() {
      // 拼装表数据
      this.resetData();
      groupList.forEach((item) => {
        const obj = {};
        this.columns.forEach((i) => {
          if (i.prop === "name") {
            obj["name"] = item.name;
          } else {
            obj[i.prop] = "";
          }
        });
        obj["part"] = item.part;
        this.tableData.push(obj);
      });
    },
    changePartNum(val) {
      let arr = [];
      if (val <= 1) {
        arr = [{ label: "1区", value: 1 }];
      } else {
        for (let i = 1; i <= val; i++) {
          arr.push({ label: i + "区", value: i });
        }
      }
      this.parts = [...arr];
    },
    generate() {
      this.tableData.forEach((item) => {
        item[this.columns[item.part].prop] = 1;
      });
    },
  },
  created() {
    this.init();
  },
};
</script>
<style scoped>
.hello {
  padding: 20px;
}
</style>
